import uniFloatingButton from "@/components/uni-floating-button/uni-floating-button.vue"
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import { mapGetters } from 'vuex';
export default {
	components: {
		uniLoadMore,
		uniDrawer,
		uniFloatingButton
	},
	computed:{
		
		...mapGetters('good',{
			recommend:'recommend'
		})
	},
	onLoad:function(options){
		this.getData()
	},
	data() {
		return {
			pullDownRefresh:true,
			showFloatButton:false,
			scrollTop:0,
			oldScrollTop:0,
			canFix:true,
			startPrice:'',
			endPrice:'',
			showDrawer: false,
			params:{},
			selectIndex:0,
			loadmoreStatue:'more',
			loadingText: {
				contentdown: '下拉加载更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			conditions:[
				{
					param:'score',
					name:'人气'
				},
				{
					param:'time',
					name:'最新'
				},
				{
					param:'sales_num',
					name:'销量'
				},
				{
					param:'price',
					name:'价格'
				},
				{
					param:'',
					name:'筛选'
				}
			]
		}
	},
	methods: {
		closeDrawer(e) {
			this.showDrawer = false;
		},
		async conditionClick(index){
			if(index == this.conditions.length - 1){
				this.showDrawer = true;
				return;
			}
	
			this.selectIndex = index;
			let condition = this.conditions[index];
			let params = {}
			params['page'] = 1;
			params[condition.param] = this.params[condition.param] == 1 ? 2 : 1;
		
			this.params = params;
			
			uni.showLoading({
				title: '数据加载中...'
			});
			await this.$store.dispatch('good/GetRecommend',this.params)
			uni.hideLoading();
			this.gotTop();
		},
		async getData(){
			uni.showLoading({
				title:"数据加载中..."
			})
			this.params = {
				page:1,
				push:true
			}
			await this.$store.dispatch('good/GetRecommend',this.params)
			this.gotTop()
			uni.hideLoading();
		},
		async loadMore() {
			this.params.push = true;
			this.params.page = this.recommend.page + 1;
			this.loadmoreStatue = 'loading';
			this.$store.dispatch('good/GetRecommend',this.params).then(()=>{
				this.loadmoreStatue = 'more';	
			}).catch(()=>{
				this.loadmoreStatue = 'noMore';	
			})
		},
		onKeyInput(e){
			
		},
		
		reset(){
			console.log(this.startPrice)
			console.log(this.endPrice)
			this.startPrice='';
			this.endPrice ='';
		},
		goodClick(g){
			// uni.navigateTo({
			// 	url: '/pages/good/detail?id='+g.id+"&nummId="+g.goods_id
			// })
			
			let params = {
				id: g.id,
				numm_id: g.goods_id
			};
			uni.showLoading({
				title: '数据加载中...'
			});
			this.$store.dispatch('good/GetGoodDetail', params).then(()=>{
				uni.hideLoading();
				uni.navigateTo({
					url: '/pages/good/detail?id='+g.id+"&nummId="+g.goods_id
				})
				
			}).catch(()=>{
				//TODO
				uni.hideLoading();
			});
		},
		fix(){
			if(this.endPrice < this.startPrice){
				this.canFix = false;
				return;
			}
			this.canFix = true;
			let params = {}
			params['page'] = 1;
			params['start_price'] = this.startPrice;
			params['end_price'] = this.endPrice;
			this.params = params;
			uni.showLoading({
				title: '数据加载中...'
			});
			this.$store.dispatch('good/GetRecommend',this.params).then(()=>{
				uni.hideLoading();
				this.showDrawer = false;
			}).catch(()=>{
				uni.hideLoading();
				this.showDrawer = true;
			});
		},
		scroll(e){
			// this.disabledPullRefresh(e.detail.scrollTop == 0);
			if(e.detail.scrollTop == 0){
				this.pullDownRefresh = true;
				this.disabledPullRefresh(true);
			}else{
				//保证只设置一次
				if(!this.pullDownRefresh){
					this.disabledPullRefresh(false);
				}
				this.pullDownRefresh = false;
			}
			if(e.detail.scrollTop > 1000){
				this.showFloatButton = true;
			}else{
				this.showFloatButton = false;
			}
			this.oldScrollTop = e.detail.scrollTop;
		},
		gotTop(){
			this.scrollTop = this.oldScrollTop;
			this.$nextTick(function() {
                this.scrollTop = 0
            });
			// this.scrollTop = 0;
		}
	},
	
	/**
	 * 当 searchInput 配置 disabled 为 true 时触发
	 */
	onNavigationBarSearchInputClicked(e) {
		console.log('事件执行了')
		uni.navigateTo({
			url: '/pages/search/index'
		});
	},
	/**
	 *  点击导航栏 buttons 时触发
	 */
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url: '/pages/center/footprint'
		});
	},
	onTabItemTap(e){
		console.log(e)
		if(e.index == 1){
			this.getData()
		}
	},
	onPullDownRefresh() {
			this.params.page = this.recommend.page + 1;
			this.params.push = false;
			this.$store.dispatch('good/GetRecommend',this.params).then(()=>{
				uni.stopPullDownRefresh();
			}).catch(()=>{
				uni.stopPullDownRefresh();
			})
	}
};
